import styled from "@emotion/styled/macro";
import DatePicker from "react-datepicker";
import { ReactComponent as ChevronDown } from '../../icons/chevron-down.svg';
import "react-datepicker/dist/react-datepicker.css";

export const Section = styled.section`
  display: flex;
  flex-direction: column;
  margin: auto;
  padding: 0;
  max-width: 272px;

  @media screen and (min-width: 768px) {
    max-width: 688px;
   }
  @media screen and (min-width: 1280px) {
    max-width: 1280px;
   }
`;

export const Title = styled.h2`
  font-family: Poppins;
  font-weight: 600;
  font-size: 24px;
  color: var(--textColor);
  margin-bottom: 24px;
  display: flex;
  align-self: flex-start;

    @media screen and (min-width: 768px) {
      font-size: 32px;
    }

     @media screen and (min-width: 1280px) {
      margin-bottom: 25px;
    }
`;

export const Form = styled.form`
    padding: 40px 16px;
    width: 272px;
    border-radius: 8px;
    box-shadow: 2px 4px 9px rgba(166, 141, 174, 0.28);
    background-color: var(--bgdColor);
    
  @media screen and (min-width: 768px) {
      padding: 40px 24px;
      width: 688px;
  }

  @media screen and (min-width: 1280px) {
    width: 1280px;
}

`;
export const FormWrapper = styled.div `
    display: flex;
    flex-direction: column;
    justufy-content: center;

  @media screen and (min-width: 768px) {
    display: grid;   
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 0 24px;
    justify-content: center;

    & > :nth-child(2) {
      grid-row: span 2;
      grid-column-start: 1;
    }

    & > :nth-child(5) {
      grid-column-start: 2;
      grid-row-start: 1;
    }

    & > :nth-child(6) {
      grid-column-start: 2;
      grid-row-start: 2;
    }

    & > :nth-child(7) {
      grid-column-start: 2;
      grid-row-start: 3;
    }
    & > :nth-child(8) {
      grid-column-start: 2;
      grid-row-start: 4;
    }

    & > :nth-child(10) {
      grid-column-start: 2;
      grid-row-start: 4;
    }
  }

  @media screen and (min-width: 1280px) {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 0 42px;
    align-content: center;
     

    & > :nth-child(3) {
      grid-column-start: 2;
      grid-row-start: 1;
    }

    & > :nth-child(4) {
      grid-column-start: 2;
      grid-row-start: 2;
    }

    & > :nth-child(5) {
      grid-column-start: 2;
      grid-row-start: 3;
    }
    & > :nth-child(6) {
      grid-column-start: 3;
      grid-row-start: 1;
    }
    & > :nth-child(7) {
      grid-column-start: 3;
      grid-row-start: 2;
    }
    & > :nth-child(8) {
      grid-column-start: 3;
      grid-row-start: 3;
    }
  }
}
`;

export const Label = styled.label`
    position: relative;
    overflow: hidden;
    display: flex;
    flex-direction: column;

    @media screen and (min-width: 768px) {
      width: 308px;
     
  }
  @media screen and (min-width: 1280px) {
      width: 372px;
}

    & > span {
    font-family: poppins;
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    color: var(--accentColor);
    margin-bottom: 8px;
    }

    & > p {
       font-family: poppins;
        font-weight: 400;
        font-size: 12px;
        line-height: 16px;
        color: var(--highColor);
        text-align: right;
        display: none;
    }

    & > input,
    & > textarea {
        margin-bottom: 20px;
        padding: 16px 12px;
        border-radius: 8px;
        border: 1px solid var(--dividerColor);
        font-family: poppins;
        font-weight: 400;
        font-size: 16px;
        line-height: 24px;
        color: var(--textColor);

      &:invalid {
            margin-bottom: 0;
            border: 1px solid var(--highColor);
        }
      }
      

     & > input:invalid ~ p,
     & > textarea:invalid ~ p {
         display: block;
     }

    & > select {
        margin-bottom: 20px;
        padding: 16px 12px;
        border-radius: 8px;
        border: 1px solid var(--dividerColor);
        font-family: poppins;
          font-weight: 400;
          font-size: 16px;
          line-height: 24px;
          color: var(--textColor);

        & > option {
          font-family: poppins;
          font-weight: 400;
          font-size: 16px;
          line-height: 24px;
          color: var(--textColor);
        }
    } 

    & > input::placeholder,
    & > textarea::placeholder {
      font-family: poppins;
      font-weight: 400;
      font-size: 16px;
      line-height: 24px;
      color: var(--dividerColor);
    }
    
`;
export const DatePickerWrapper = styled.div`
    position: relative;
    margin-bottom: 20px;
    padding: 16px 12px;
    border-radius: 8px;
    width: 100%;
    height: 56px;
    border: 1px solid var(--dividerColor);

    @media screen and (min-width: 768px) {
      max-width: 308px;
     }
    @media screen and (min-width: 1280px) {
      width: 1280px;
     }


    & .react-datepicker-ignore-onclickoutside {
      border: none;
      width: 100%;
      height: 100%;
    }
    & .input {
      width: 100%;
      height: 100%;
      border: none;
    }

    & .react-datepicker__input-container {
      border: none;
      width: 100%
      height: 100%;

      & > input {
        border: none;
        outline: none;
      }
    }

    
    & .react-datepicker-wrapper {
      width: 100%;   
  }

    & .react-datepicker__triangle {
    display: none;
  }
     
    & .react-datepicker {
      display: flex;
      flex-direction: column;
      width: 240px;
      height: 348px;
      padding: 20px;
      border-radius: 11px;
      background-color: var(--bgdColor);
      box-shadow: 0px 0px 14px 0px rgba(0, 0, 0, 0.07);
      border: none;
    
      @media screen and (min-width: 768px) {
          width: 308px;
        }
    
        @media screen and (min-width: 1440px) {
          width: 373px;
        }

        & > react-datepicker--time-only {
          width: 240px;
        }
    
      }

      & .react-datepicker__input-container {
        width: 100%;
        heigt: 100%;
        border: none;
        font-family: 'Poppins';
        font-weight: 400;
        font-size: 16px;
        line-height: 24px;
        color: var(--textColor);
        
        &:focus {
          border: none;
        }
      }

      & .react-datepicker__header {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        background-color: var(--bgdColor);
        border: none;
        height: 56px;
        padding: 0;
      }

      & .react-datepicker__current-month {
        font-family: 'Poppins';
        font-weight: 500;
        font-size: 14px;
        line-height: 20px;
        color: var(--textColor);
        margin-bottom: 16px;
      }

    & .react-datepicker__navigation--previous {
      left: 16px;
      top: 16px;
    }
  
    & .react-datepicker__navigation--next {
      right: 16px;
      top: 16px;
    }
  
    & .react-datepicker__navigation-icon::before {
      border-color: var(--textColor);
      border-width: 2px 2px 0 0;
     
    }

    & .react-datepicker__day-names {
      display: flex;
      justify-content: space-between;
      width: 100%;
      padding: 0;
      color: var(--textColor);
  }

    & .react-datepicker__day-name {
      font-family: 'Poppins';
      font-weight: 400;
      font-size: 12px;
      line-height: 16px;
      color: var(--textColor);
      margin-bottom: 16px;
      
        &:first-child,
        &:last-child {
        color: var(--highColor);
        }
    }

    & .react-datepicker__month {
      margin: 16px 0px 16px 0px;
      @media screen and (min-width: 768px) {
        padding: 0px;
      }
    }

    & .react-datepicker__week {
      display: grid;
      grid-template-columns: repeat(7, 1fr);
      
      &:not(:last-child) {
        margin-bottom: 4px;
      }

    & .react-datepicker__day {
      width: 29px;
      height: 32px;
      margin: 0;
      padding: 8px;
      text-align: center;
      font-family: 'Inter';
      font-weight: 400;
      font-size: 12px;
      line-height: 16px;
      color: var(--dividerColor);
      border-radius: 0;

      &:hover {
        background-color: var(--accentColor);
        color: var(--bgdColor);
      }

      @media screen and (min-width: 768px) {
        width: 38px;
      }
  
      @media screen and (min-width: 1280px) {
        width: 47px;
      }
    }

    & .react-datepicker__day--selected {
      background-color: var(--accentColor);
      color: var(--bgdColor);
    }

    & .react-datepicker__day--today {
      background-color: var(--bgdColor);
      color: var(--accentColor);
      font-weight: 600;
    }

    & .react-datepicker__children-container {
        width: 100%;
        padding: 20px;
  }

    & .react-datepicker__time-list {
      width: 100%;
    }
   & .react-datepicker__aria-live {
    border: none;
    outline: none;
   }
  `
export const StyledDatePicker = styled(DatePicker)`
  width: 100%;
  height: 100%;
  border: none;
  &:focus {
    border: none;
  }
`;

export const StyledChevronDown = styled(ChevronDown)`
  position: absolute;
  right: 12px;
  top: 30%;
  stroke: var(--accentColor);

`;

export const BtnWrapper = styled.div`
  display: flex;  
  align-items: center;
  justify-content: flex-start;

  @media screen and (min-width: 768px) {
    flex-direction: flex-end;
    justify-content: right;
  }
`;

export const DateButton = styled.button`
display: inline-block;
text-align: center;
justify-content: center;
width: 110px;
height: 32px;
background-color: var(--accentColor);
padding: 8px 16px;
font-family: poppins;
font-weight: 500;
font-size: 12px;
line-height: 16px;
color: var(--bgdColor);
border: none;
border-radius: 8px;
cursor: pointer;

&:hover {
  background-color: var(--btnHover);
}
`;  

export const CancelButton = styled.button`
  display: flex;
  text-align: center;
  justify-content: center;
  text-decoration: none;
  width: 75px;
  height: 32px;
  background-color: var(--bgdColor);
  padding: 8px 16px;
  margin-right: 16px;
  font-family: poppins;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  color: var(--accentColor);
  border: 1px solid var(--accentColor);
  border-radius: 8px;
 

  &:hover {
    background-color: var(--btnHover);
    color: var(--bgdColor);
  }
`; 

export const Button = styled.section`
  display: block;
  text-align: center;
  justify-content: center;
  width: 240px;
  height: 56px;
  background-color: var(--accentColor);
  padding: 16px;
  font-family: poppins;
  font-weight: 500;
  font-size: 16px;
  color: var(--bgdColor);
  border-radius: 8px;


&:hover {
  background-color: var(--btnHover);
  color: var(--bgdColor);
}

@media screen and (min-width: 768px) {
  grid-row-start: 6;
  grid-column-start: 2;
  justify-self: end;
  margin-top: 40px;
 }
@media screen and (min-width: 1280px) {
  grid-column-start: 3;
  grid-row-start: 4;
  margin-top: 60px;
 }
`;